




























































































import Vue from 'vue'
import MediaStylePalette, { Color } from '../../'

export default Vue.extend({
  name: 'app',
  data () {
    return {
      colors: {
        left: {
          primaryColor: '',
          secondaryColor: '',
          backgroundColor: ''
        },
        right: {
          primaryColor: '',
          secondaryColor: '',
          backgroundColor: ''
        },
        top: {
          primaryColor: '',
          secondaryColor: '',
          backgroundColor: ''
        },
        bottom: {
          primaryColor: '',
          secondaryColor: '',
          backgroundColor: ''
        }
      },
      imgSrc: '',
      mediaStylePalette: new MediaStylePalette()
    }
  },
  methods: {
    setActive (position: 'left' | 'right' | 'top' | 'bottom') {
      for (const k of Object.keys(this.colors)) {
        if (k === position) {
          this.$set(this.colors[k], 'active', 'active')
        } else {
          this.$set(this.colors[k as 'left' | 'right' | 'top' | 'bottom'], 'active', '')
        }
      }
    },
    a0 (color: Color) {
      if (!color) return ''
      return `rgba(${color.rgb[0]}, ${color.rgb[1]}, ${color.rgb[2]}, 0)`
    },
    onImageSelected (e: any) {
      const files = e.target.files
      if (files && files[0]) {
        const reader = new FileReader()
        reader.onload = (e: any) => {
          this.imgSrc = e.target.result
        }
        (this.$refs.img as HTMLImageElement).onload = () => {
          this.mediaStylePalette
            .from(this.$refs.img as HTMLImageElement)
          const v = [
            { n: 'left', d: -90 },
            { n: 'right', d: 90 },
            { n: 'top', d: 0 },
            { n: 'bottom', d: 180 }
          ]
          for (const p of v) {
            this.mediaStylePalette
              .use({
                direction: p.d
              })
              .getPalette()
              .then(palette => {
                this.$set(this.colors, p.n, palette)
              })
          }
        }
        reader.readAsDataURL(files[0])
      }
    }
  }
})
